import {Component, OnInit} from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {FormService} from "../services/form.service";

@Component({
  selector: "app-solicitation",
  templateUrl: "./solicitation.component.html",
  styleUrls: ["./solicitation.component.scss"]
})
export class SolicitationComponent implements OnInit {
  protected id;
  protected model = {};
  protected isLoading: boolean;
  protected isCompleted : boolean;

  constructor(private router: Router,
              private route: ActivatedRoute,
              private formService: FormService) {
    this.id = this.route.snapshot.params["id"];
    this.isLoading = true;
  }

  ngOnInit() {
    this.formService.getById(this.id).subscribe((data) => {
        this.model = data;
        this.isLoading = false;

      if(data.status === "COMPLETED" || data.status === "DENIED") {
        this.isCompleted = true;
      }

      }, error => {
        console.error(error);
      }
    );
  }

  deleteForm(id) {
    this.formService.deleteById(id).subscribe((data) => {
      console.log(data);
    }, error => {
      console.log(error);
    });
  }

  goBack() {
    this.router.navigate(["/home/solicitations"]);
  }

  updateForm(id) {
    this.isLoading = true;
    this.formService.update(id, this.model).subscribe((data) => {
      this.isLoading = false;
    }, error => {
      console.log(error);
    });
  }

}



