import { RouterModule, Routes } from '@angular/router';
import { SolicitationComponent } from './solicitation.component';
import { ModuleWithProviders } from '@angular/core';

const solicitationRoutes: Routes = [
    {
        path: '', component: SolicitationComponent
    }
];

export const solicitationRouting: ModuleWithProviders = RouterModule.forChild(solicitationRoutes);



